<template>
    <div class="container">
        <!-- <Header title="充值">
            <div class="fl" slot="left">
                <span></span>
                <var>返回</var>
            </div>
        </Header> -->
        <div class="recharge-page">
            <div class="user-info">
                <div class="fl">
                    <span>用户名：</span>
                    <span class="c1">{{UserBaseInfo.result == 108 ? '未登录' : UserBaseInfo.account || '加载中'}}</span>
                    <button
                        v-clipboard:copy="UserBaseInfo.account"
                        v-clipboard:success="onCopy"
                    >复制</button>
                </div>
                <div class="fr">
                    <span>余额：</span>
                    <span class="c1">{{UserBaseInfo.result == 108 ? '未登录' : UserBaseInfo.balance >= 0 ? UserBaseInfo.balance : '加载中'}}</span>
                    <span>元</span>
                    <i @click="getUserBaseInfo(true)" :class="['ico-ref', {'status': status}]"></i>
                </div>
            </div>
            <div class="chong-money">
                <div class="one">
                    <span class="fl">充值金额：</span>
                    <myNumberInput :point="3" :max="99999" @input="oninput" placeholder="请手动输入充值金额" v-model="inputMoney" :auto="false" />
                    <span class="fr">元</span>
                </div>
                <div class="two" v-if="false">
                    <ul>
                        <li @click="index=0; typeMoney=''; inputMoney=50" :class="{'acti':index==0}">
                            <span>￥{{money[0]}}元</span>
                        </li>
                        <li @click="index=1; typeMoney=''; inputMoney=100" :class="{'acti':index==1}">
                            <span>￥{{money[1]}}元</span>
                        </li>
                        <li @click="index=2; typeMoney=''; inputMoney=200" :class="{'acti':index==2}">
                            <span>￥{{money[2]}}元</span>
                        </li>
                        <li @click="index=3; typeMoney=''; inputMoney=500" :class="{'acti':index==3}">
                            <span>￥{{money[3]}}元</span>
                        </li>
                        <li @click="index=4; typeMoney=''; inputMoney=1000"  :class="{'acti':index==4}">
                            <span>￥{{money[4]}}元</span>
                        </li>
                        <li @click="index=5; typeMoney=''; inputMoney=5000" :class="{'acti':index==5}">
                            <span>￥{{money[5]}}元</span>
                        </li>
                        <li @click="index=6; typeMoney=''; inputMoney=10000" :class="{'acti':index==6}">
                            <span>￥{{money[6]}}元</span>
                        </li>
                        <li @click="index=7; inputMoney=typeMoney" :class="{'acti':index==7}">
                            <!-- <input type="text" maxlength="7" v-model="typeMoney" placeholder="自定义金额"> -->
                            <b-input v-model="typeMoney" placeholder="自定义金额"></b-input>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="method">
                <div style="width: 100%; height: 0.2rem; background-color:#F7F7F7 "></div>
                <h3 style="font-size: 0.25rem;background-color: #FFF7E8;color: #F2B777;line-height: 0.5rem;padding: 0.2rem 0.2rem;margin: 0.1rem 2%;border-radius: 0.1rem;float: left;width: 96%;"><img style="height: 0.3rem; margin-right: 0.1rem;" src="../../static/img/cztb/xx.png">
                    银行卡充值送现金，彩金直接到账，无需申请
                </h3>
                <template v-if="czTypeList">
                    <div class="tabs">
                        <div v-for="(item, index) in czTypeList" :key="index" @click="tabs=index" :class="{'acti': tabs==index}">
                            <div v-if="item.type == 1" style="float: left;border: none;margin: 0;padding: 0;">
                                <img style="position: absolute;height: 0.35rem;" src="../../static/img/cztb/jiang.png">
                            </div>
                            <img v-if="item.type == 1" style="height: 0.7rem;margin: 0.1rem 0;" src="../../static/img/cztb/bank.png">
                            <img v-if="item.type == 2" style="height: 0.7rem;margin: 0.1rem 0;" src="../../static/img/cztb/alipay.png">
                            <img v-if="item.type == 3" style="height: 0.7rem;margin: 0.1rem 0;" src="../../static/img/cztb/wxpay.png">
                            <img v-if="item.type == 4" style="height: 0.7rem;margin: 0.1rem 0;" src="../../static/img/cztb/ysfpay.png">
                            <p style="font-size: 0.27rem;color: #000;height: 0.56rem;width: 100%; line-height: 0.28rem; overflow: hidden; text-overflow: ellipsis;">
                                {{item.name}}
                            </p>
                            <p v-if="item.type == 1" style="font-size: 0.25rem;line-height: 0.39rem;color:#999;">
                                充值送2.2%
                            </p>
                            <p v-if="item.type == 2" style="font-size: 0.25rem;line-height: 0.39rem;color:#999;">
                                方便快捷
                            </p>
                            <p v-if="item.type == 3" style="font-size: 0.25rem;line-height: 0.39rem;color:#999;">
                                方便快捷
                            </p>
                            <p v-if="item.type == 4" style="font-size: 0.25rem;line-height: 0.39rem;color:#999;">
                                充值送2.2%
                            </p>
                            <div style="float: right;border: none;margin: 0;padding: 0;">
                                <img class="actiImg" style="display: none;position: relative;height: 0.28rem;bottom: 0.14rem;" src="../../static/img/cztb/xz.png">
                            </div>
                        </div>
                        <div v-if="agentList != ''" :key="czTypeList.length + 1" @click="rgcz" :class="{'acti': tabs==czTypeList.length + 1}">
                            <div style="float: left;border: none;margin: 0;padding: 0;">
                                <img style="position: absolute;height: 0.35rem;" src="../../static/img/cztb/you.png">
                            </div>

                            <img style="height: 0.7rem;margin: 0.1rem 0;" src="../../static/img/cztb/maypay.png">
                            <p style="font-size: 0.27rem;color: #000;height: 0.56rem;">
                                人工充值
                            </p>
                            <p style="font-size: 0.25rem;line-height: 0.39rem;color:#999;">
                                方便快捷
                            </p>
                            <div style="float: right;border: none;margin: 0;padding: 0;">
                                <img class="actiImg" style="display: none;position: relative;height: 0.28rem;bottom: 0.14rem;" src="../../static/img/cztb/xz.png">
                            </div>
                        </div>
                    </div>
                    <h3 v-if="tabs == czTypeList.length + 1" style="font-size: 0.25rem;background-color: #E8F5FD;color: #3290E8;line-height: 0.5rem;padding: 0.2rem 0.2rem;margin: 0.1rem 0.2rem;border-radius: 0.1rem;float: left;">
                        请勿添加QQ群或微信群!人工充值只负责充值不受理其他业务，如有代充向您推荐其他平台，举报属实有奖！
                    </h3>
                    <div class="item-wrapper">
                        <div class="item-wrap">
                            <template v-for="(item, index) in czTypeList">
                                <div class="item" v-if="tabs==index" :key="index">
                                    <ul>
                                        <template v-for="(item1, index1) in item.czList">
                                            <li @click="selectPay(item1)" v-if="item1.name" :key="index1">
                                                <div class="pict">
                                                    <img v-if="item1.src" :src="item1.src" alt="">
                                                    <img v-else src="../../static/img/6.jpg" alt="">
                                                </div>
                                                <div class="text">
                                                    <h3>{{item1.name}}</h3>
                                                    <p>{{item1.remarks}}</p>
                                                </div>
                                            </li>
                                        </template>
                                    </ul>
                                </div>
                            </template>
                            <div class="item" v-if="tabs==czTypeList.length + 1" :key="czTypeList.length + 1">
                                <ul>
                                    <template v-for="(item1, index1) in agentList">
                                        <li v-if="item1.account" :key="index1" style="padding: .15rem .1rem .05rem .1rem;">
                                            <div class="a-text">
                                                <p style="font-size: 0.3rem;line-height: 0.5rem;">{{item1.account}}</p>
                                                <p style="font-size: .2rem;color: #999;line-height: 0.5rem;">{{item1.email}}</p>
                                            </div>
                                            <div class="a-pic">
                                                <div v-if="item1.qq !=''" @click="qqTest(1)">
                                                    <img src="../../static/img/cztb/qq.png" v-clipboard="item1.qq">
                                                    <p style="color: #369BCE;" v-clipboard="item1.qq">立即联系</p>
                                                </div>
                                                <div v-if="item1.qq ==''" @click="qqTest(4)">
                                                    <img src="../../static/img/cztb/qq_hui.png">
                                                    <p style="color: #AAAAAA;">立即联系</p>
                                                </div>
                                                <div v-if="item1.weixin !=''" @click="qqTest(2)">
                                                    <img src="../../static/img/cztb/wx.png" v-clipboard="item1.weixin">
                                                    <p style="color: #4CBF00;" v-clipboard="item1.weixin">立即联系</p>
                                                </div>
                                                <div v-if="item1.weixin ==''" @click="qqTest(5)">
                                                    <img src="../../static/img/cztb/wx_hui.png">
                                                    <p style="color: #AAAAAA;">立即联系</p>
                                                </div>
                                                <div v-if="item1.payTreasure != ''" @click="qqTest(3)">
                                                    <img src="../../static/img/cztb/zfb.png" v-clipboard="item1.payTreasure">
                                                    <p style="color: #06B4FD;" v-clipboard="item1.payTreasure">立即联系</p>
                                                </div>
                                                <div v-if="item1.payTreasure == ''" @click="qqTest(6)">
                                                    <img src="../../static/img/cztb/zfb_hui.png">
                                                    <p style="color: #AAAAAA;">立即联系</p>
                                                </div>
                                            </div>
                                        </li>
                                    </template>
                                </ul>
                            </div>
                        </div>
                    </div>
                </template>
                <div class="no-loadding" v-else-if="UserBaseInfo.result == 108">充值方式需要登录</div>
                <loadding v-else></loadding>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        GAME_GET_USER_BASE_INFO
    } from "@/tool/common/constants.js";
    import {
        companyShortName
    } from "@/tool/utils/request.js";
    import myNumberInput from '@/tool/plug/component/form/myNumberInput';

    import { mapState } from "vuex";
    export default {
        components:{
            myNumberInput
        },
        data(){
            return {
                index: -1,

                money:[50, 100, 200, 500, 1000, 5000, 10000, ''],

                status:false,

                typeMoney:'',
                agentList:"",
                czSource:"",
                tabs:0,

                czTypeList: false,

                inputMoney: '',

            }
        },
        computed:{
            ...mapState('Game', {
                UserBaseInfo: state => state[GAME_GET_USER_BASE_INFO],
            }),
            ...mapState('Recharge',{
                user: state => state.user
            })
        },
        watch: {
            typeMoney(new_value){
                this.money[7] = new_value;
                if(new_value)
                this.inputMoney = new_value
            },
            czSource(newv){
                if(newv != ""){
                    console.log("s1s")
                    this.forList(newv)
                }
            }
        },
        created(){
            this.$store.commit('Recharge/SET_USER', this.$route.query)
            this.getUserBaseInfo()
            this.getAppCzInfo4()
            this.getAgentList()
            // this.inputMoney = this.money[0];
        },
        mounted(){
            if(this.env.app){
                var json = `{"title":"充值","rightTitle":{"name" :"充值记录","url":"https://anyua.cc:8888/#/rechargeRecord?uid=${this.user.uid}&token=${this.user.token}"},"showNai":"y","back":"app"}`;
                this.callHandle(json);
            }

        },
        methods: {
            rgcz(){
                if(this.tabs != this.czTypeList.length + 1){
                    this.tabs=this.czTypeList.length + 1
                    this.mui.confirm(
                        "&nbsp;&nbsp;&nbsp;\n" +
                        "1.代充只负责充值，如果您有其他问题，请联系我们在线客服处理。代充如果对您有骚扰或其他行为，请向我们在线客服举报，举报有奖。\n" +
                        "&nbsp;&nbsp;&nbsp;\n" +
                        "2.代充人员不定期更新，为避免您不必要的损失，每次充值请在平台菜单上重新操作订单，以官方为准。 \n" +
                        "&nbsp;&nbsp;&nbsp;\n" +
                        "3.充值时请向代充人员提供您的用户名(您的用户名在页面复制)。充值完成后，请重新进入平台查看余额。",
                        "代充提示",
                        ["我知道了"]
                    );
                }

            },
            qqTest(a){
                switch (a) {
                    case 1:
                        if(this.env.app){
                            var json = 'qq'
                            this.$toast('QQ号已复制，请前往QQ联系代充，完成付款.3秒后将打开QQ');
                            setTimeout(() => {
                                if(this.env.isAndroid){
                                    window.android.openChat(json);
                                }
                                if(this.env.isIOS){
                                    window.webkit.messageHandlers.openChat.postMessage(json);
                                }
                            }, 2500);
                        }else{
                            window.open('qq://')
                        }
                        break;
                    case 2:
                        if(this.env.app){
                            var json1 = 'wx'
                            this.$toast('微信号已复制，请前往微信联系代充，完成付款.3秒后将打开微信');
                            setTimeout(() => {
                                if(this.env.isAndroid){
                                    window.android.openChat(json1);
                                }
                                if(this.env.isIOS){
                                    window.webkit.messageHandlers.openChat.postMessage(json1);
                                }
                            }, 3000);
                        }else{
                            window.open('weixin://')
                        }
                        break;
                    case 3:
                        if(this.env.app){
                            var json2 = 'zfb'
                            this.$toast('支付宝账号已复制，请前往支付宝联系代充，完成付款.3秒后将打开支付宝');
                            setTimeout(() => {
                                if(this.env.isAndroid){
                                    window.android.openChat(json2);
                                }
                                if(this.env.isIOS){
                                    window.webkit.messageHandlers.openChat.postMessage(json2);
                                }
                            }, 3000);

                        }else{
                            window.open('alipay://')
                        }
                        break;
                    case 4:
                        this.$toast('该代理商尚未开启QQ通道，请选择其他方式联系')
                        break;
                    case 5:
                        this.$toast('该代理商尚未开启微信通道，请选择其他方式联系')
                        break;
                    case 6:
                        this.$toast('该代理商尚未开启支付宝通道，请选择其他方式联系')
                        break;
                }
            },
            onCopy(){
                this.$toast('复制成功')
            },
            getAgentList(){
                this.$api.user.getAgentList(this.user)
                    .then(res=>{
                        if(res.result==1){
                            this.agentList = res.list
                        }
                    })

            },
            // 选择充值方式
            selectPay(item){


                console.group(item.name);
                console.log(item);
                console.groupEnd();

                if(this.inputMoney == ''){
                    this.$toast('请先输入金额');
                    return false;
                }

                const money = this.inputMoney;

                this.$store.commit('Recharge/SET_MONEY', money);

                const { uid, token } = this.user
                var json
                switch (item.payType) {
                    case 1:
                        if(item.url){
                            // 链接跳转
                            if (money >= item.minMoney && money <= item.maxMoney){
                                var url = `${item.url}?uid=${uid}&payOnlineId=${item.payOnlineId}&money=${money}&companyShortName=${companyShortName}&token=${token}`
                                this.openBrowser(url)
                            }else{
                                this.$toast(`该充值方式限额${item.minMoney}到${item.maxMoney}`)
                            }
                        }else if (item.type == 1){
                            // 微信转账
                            if(this.env.app){
                                json = '{"title":"微信转账","rightTitle":{"name" :"","url":""},"showNai":"y","back":"h5"}'
                                this.callHandle(json)
                                this.$router.push('/transferWx')
                            }else{
                                this.$router.push('/transferWx')
                            }
                        }
                        break;
                    case 2:
                        if(item.url){
                            // 链接跳转
                            if (money >= item.minMoney && money <= item.maxMoney){
                                var url1 = `${item.url}?uid=${uid}&payOnlineId=${item.payOnlineId}&money=${money}&companyShortName=${companyShortName}&token=${token}`
                                this.openBrowser(url1)
                            }else{
                                this.$toast(`该充值方式限额${item.minMoney}到${item.maxMoney}`)
                            }
                        }else if (item.type==1){
                            // 支付宝转账
                            if(this.env.app){
                                json = '{"title":"支付宝转账","rightTitle":{"name" :"","url":""},"showNai":"y","back":"h5"}'
                                this.callHandle(json)
                                this.$router.push('/transferZfb')
                            }else{
                                this.$router.push('/transferZfb')
                            }
                        }
                        break;
                    case 3:
                        // QQ转账
                        console.log('QqWalletTrf');
                        break;
                    case 4:
                        if(item.url){
                            // 链接跳转
                            if (money >= item.minMoney && money <= item.maxMoney){
                                var url2 = `${item.url}?uid=${uid}&payOnlineId=${item.payOnlineId}&money=${money}&companyShortName=${companyShortName}&token=${token}`
                                this.openBrowser(url2)
                            }else{
                                this.$toast(`该充值方式限额${item.minMoney}到${item.maxMoney}`)
                            }
                        }else{
                            // 大额度银行卡转账
                            if(this.env.app){
                                json = '{"title":"银行汇款","rightTitle":{"name" :"","url":""},"showNai":"y","back":"h5"}'
                                this.callHandle(json)
                                this.$router.push('/bankTransfer');
                            } else {
                                this.$router.push('/bankTransfer'); // 老的
                            }
                        }
                        break;
                    case 9:
                        // 微信转银行卡
                        if(this.env.app){
                            json = '{"title":"微信转银行卡","rightTitle":{"name" :"","url":"/bankCard/courseWx"},"showNai":"y","back":"h5"}'
                            this.callHandle(json)
                            this.$router.push('/bankCardWx')
                        }else{
                            this.$router.push('/bankCardWx')
                        }
                        break;
                    case 10:
                        // 云闪付转账
                        if(this.env.app){
                            json = '{"title":"云闪付转账","rightTitle":{"name" :"","url":""},"showNai":"y","back":"h5"}'
                            this.callHandle(json)
                            this.$router.push('/payment');
                        }else{
                            this.$router.push('/payment');
                        }
                        break;
                    case 11:
                        // 支付宝转银行卡
                        if(this.env.app){
                            json = '{"title":"支付宝转银行卡","rightTitle":{"name" :"","url":"/bankCard/courseWx"},"showNai":"y","back":"h5"}'
                            this.callHandle(json)
                            this.$router.push('/bankCardZfb')
                        } else {
                            this.$router.push('/bankCardZfb')
                        }
                        break;
                    default:
                        break;
                }
            },
            openBrowser(url){
                if(this.env.app){
                    var json = `{"url":"${url}","type":"1"}`
                    if(this.env.isAndroid){
                        window.android.openBrowser(json);
                    }
                    if(this.env.isIOS){
                        window.webkit.messageHandlers.openBrowser.postMessage(json);
                    }
                }else{
                    window.open(url)
                }
            },
            // 个人信息
            getUserBaseInfo(refresh){
                if(refresh){
                    this.status = true
                }
                this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, this.user)
                .then(()=>{
                    if(refresh){
                        this.status = false
                        this.$toast('加载已完成!')
                    }
                })
            },
            // 充值方式
            getAppCzInfo4(){
                this.$api.recharge.getAppCzInfo4(this.user)
                .then(res=>{
                    this.czTypeList = res.czTypeList
                    if(res.result==1) {
                        this.czSource = res.czTypeList
                        for (let i = 0; i < this.czTypeList.length; i++) {
                            let name = this.czTypeList[i].name
                            if(name.indexOf("支付宝") != -1){
                                this.czTypeList[i].type = 2
                            }else if(name.indexOf("微信") != -1 || name.indexOf("QQ") != -1){
                                this.czTypeList[i].type = 3
                            }else if(name.indexOf("银行卡") != -1){
                                this.czTypeList[i].type = 1
                            }else if(name.indexOf("云闪付") != -1 || name.indexOf("京东") != -1){
                                this.czTypeList[i].type = 4
                            }
                        }
                    }
                })
            },
            forList(czTypeList){
                czTypeList.forEach((item1, index1)=>{
                    item1.czList.forEach((item2, index2)=>{
                        if(item2.imageId){
                            this.getImage(index1, index2, item2.imageId)
                        }
                    })
                })
            },
            // 获取图标
            getImage(index1, index2, imageId){
                this.$api.upload.getImageData({id: imageId})
                .then(res=>{
                    this.$set(this.czTypeList[index1]['czList'][index2],'src','data:image/jpeg;base64,' + res.imageData)
                })
            },
        },
    }
</script>

<style>
    .van-toast__text{
        font-size: 0.25rem;
    }
.mui-popup-text{
    line-height: 1.3em;
    letter-spacing: 0.1em;
    text-align: left;
}
.item .a-text{
    float: left;
    width: 40%;
}
.item .a-text p{
    line-height: 1rem;
    font-size: 0.35rem;
}
.item .a-pic{
    width: 60%;
    display: flex;
}
.item .a-pic div{
    flex: 1 1;
    text-align: center;

}
.item .a-pic div img{
    height: 0.5rem;
}
.item .a-pic div p{
    line-height: 0.5rem;
    font-size: 0.25rem;
}
</style>
